import * as S from "./regSt";
import { useState, useRef, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";

import { useRecoilState } from "recoil";
import { editcafe } from "../recoil/editcafe";

import { ReactComponent as Photo } from "../svg/photo.svg";
import { ReactComponent as CloseIcon } from "../svg/close.svg";
import { ReactComponent as Plus } from "../svg/plus.svg";
import { ReactComponent as ArrowDown } from "../svg/ArrowDown.svg";
import { ReactComponent as ArrowUp } from "../svg/ArrowUp.svg";

import PVImg from "../components/common/PVImg";
import SearchModal from "../components/common/modal/SearchModal";
import Row from "../components/atoms/row";
import Header from "../components/common/header";

import { convertTime, updateDay } from "../hooks/registerHook";
import { feedEditApi } from "../util/management";

const Editcafe = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [file, setFile] = useState([]);
  const [register, setRegister] = useRecoilState(editcafe);
  const [openTime, setOpenTime] = useState("");
  const [closeTime, setCloseTime] = useState("");
  const [selectOn, setSelectOn] = useState(false);
  const [search, setSearch] = useState("");
  const [loc, setLoc] = useState({ address_name: null });
  const [searchModal, setSearchModal] = useState(false);
  const [days, setDays] = useState([]);
  const [dayarr, setDayarr] = useState([]);
  const [delImg, setDelImg] = useState([]);
  const [updateImg, setUpdata] = useState([]);

  const onLoadFile = (e) => {
    let copy = [...file];
    if (copy.length >= 5) {
      window.alert(" 5  ");
      return;
    } else {
      if (e.target.files[0]) {
        copy = [...copy, e.target.files[0]];
        setFile(copy);
        let coppy = [...updateImg, e.target.files[0]];

        const copy2 = { ...register };
        copy2.updateImageFiles = coppy;
        setRegister(copy2);
      }
    }
  };

  const deleteImg = (a, idx) => {
    const copy = [...file];
    const copy2 = { ...register };
    let copy3 = [...delImg];
    const hasfile = state.storeImageDtoList.filter(
      (item) => item.imageUrl === a
    );
    if (hasfile[0]) {
      copy3 = [...copy3, hasfile[0].imageId];
    }
    copy.splice(idx, 1);
    copy2.deleteImageIdList = copy3;
    setFile(copy);
    setRegister(copy2);
    console.log(file);
  };

  const onChange = (e) => {
    const name = e.target.name;
    const copy = { ...register };
    copy[name] = e.target.value;
    setRegister(copy);
  };

  const dayPush = async (e) => {
    e.stopPropagation();
    const copy = [...days];
    if (!days.includes(e.currentTarget.id)) {
      copy.push(e.currentTarget.id);
      setDays(copy);
    } else {
      copy.splice(days.indexOf(e.currentTarget.id), 1);
      setDays(copy);
    }
  };

  const deleteDay = async (a, i) => {
    const copy2 = { ...register };
    for await (let item of a[2]) {
      updateDay(item, copy2, "", "");
    }
    setRegister(copy2);
    const copy = [...dayarr];
    copy.splice(i, 1);
    setDayarr(copy);
  };

  const addTime = async () => {
    const copy = [...dayarr];
    const copy2 = { ...register };
    const open = await convertTime(openTime);
    const close = await convertTime(closeTime);
    for await (let item of days) {
      updateDay(item, copy2, open, close);
    }
    setRegister(copy2);
    copy.push([open, close, days]);
    setDayarr(copy);
    setOpenTime("");
    setCloseTime("");
    setDays([]);
    setSelectOn(false);
  };

  const convertDay = (j) => {
    if (j === "onMon") return "";
    if (j === "onTue") return "";
    if (j === "onWed") return "";
    if (j === "onThu") return "";
    if (j === "onFri") return "";
    if (j === "onSat") return "";
    if (j === "onSun") return "";
  };
  const input = useRef();

  const submit = async (register) => {
    console.log(register);

    feedEditApi(register)
      .then((res) => {
        console.log(res);
        navigate("/management");
      })
      .catch((err) => {
        window.alert("   ");
        // navigate("/management");
      });
  };

  useEffect(() => {
    const fetchingData = async () => {
      const copy = { ...register };
      copy.storeId = state.storeId;
      const obj = Object.keys(copy);
      obj.map((item) => {
        if (state[item]) {
          copy.item = state[item];
        }
      });

      setRegister(copy);
    };

    const fetching = async () => {
      const obj = Object.keys(state.businessHoursResDto);
      const copy = [...dayarr];
      const copy2 = { ...register };
      obj?.map((item, i) => {
        if (state?.businessHoursResDto) {
          if (item !== "etcTime") {
            const day = convertDay(item);
            const open = state?.businessHoursResDto[item]?.open;
            const close = state?.businessHoursResDto[item]?.closed;
            copy.push([open, close, day]);
            updateDay(day, copy2, open, close);
          }
        }
      });

      setDayarr(copy);
      setRegister(copy2);
    };
    const fetchingImg = async () => {
      const copy = [...file];
      if (state.storeImageDtoList) {
        for (let i = 0; i < state.storeImageDtoList.length; i++) {
          copy.push(state.storeImageDtoList[i].imageUrl);
        }
      }
      setFile(copy);
    };
    if (state) {
      fetching();
      fetchingImg();
      fetchingData();
    }
  }, []);

  return (
    <>
      <Header
        align={"center"}
        mcolor={"#8B8B8B"}
        text={" "}
        inner={"  "}
      >
        <div>
          <S.Submit
            style={{ marginRight: "15px" }}
            onClick={() => window.history.back()}
          >
            
          </S.Submit>
          <S.Submit
            disalbed={
              register.storeName == null ||
              register.siNm == null ||
              register.recommendation == null ||
              register.wifi == null ||
              register.restroom == null ||
              register.tableSize == null ||
              register.socket == null
            }
            isFill={
              register.storeName !== null &&
              register.siNm !== null &&
              register.recommendation !== null &&
              register.wifi !== null &&
              register.restroom !== null &&
              register.tableSize !== null &&
              register.socket !== null
            }
            onClick={() => submit(register)}
          >
            
          </S.Submit>
        </div>
      </Header>
      <S.Containaer>
        <Row gap={20}>
          <S.Column>
            <S.InputBox>
              <span></span>
              <div>{state.storeName}</div>
            </S.InputBox>
            <S.InputBox>
              <span></span>
              <div>{loc.address_name || state.address.fullAddress} </div>
            </S.InputBox>
            <S.Box height={168}>
              <p> </p>
              <S.PhotoBox>
                <S.FileUpload
                  onClick={() => {
                    input.current?.click();
                  }}
                >
                  <Photo />
                  <div>{file.length}/5</div>
                  <input
                    ref={input}
                    type="file"
                    accept="image/*"
                    style={{ display: "none" }}
                    onChange={(e) => onLoadFile(e)}
                  />
                </S.FileUpload>
                {file?.map((a, idx) => {
                  return (
                    <S.ImgBox key={idx}>
                      <CloseIcon
                        name={a.name}
                        onClick={() => deleteImg(a, idx)}
                      />
                      <PVImg img={a} />
                    </S.ImgBox>
                  );
                })}
              </S.PhotoBox>
            </S.Box>
            <S.Box height={128}>
              <p></p>
              <S.TimeBox>
                <S.BtnRow gap={13}>
                  <S.Btn>
                    {openTime && (
                      <p>{openTime.slice(0, 2) > 12 ? "" : ""}</p>
                    )}
                    <input
                      placeholder=" "
                      value={openTime}
                      onChange={(e) => {
                        setOpenTime(e.target.value);
                      }}
                    />
                  </S.Btn>
                  <S.Btn>
                    {closeTime && (
                      <p>{closeTime.slice(0, 2) > 12 ? "" : ""}</p>
                    )}
                    <input
                      placeholder=" "
                      value={closeTime}
                      onChange={(e) => {
                        setCloseTime(e.target.value);
                      }}
                    />
                  </S.Btn>
                  <S.Btn2 isT={selectOn} onClick={() => setSelectOn(!selectOn)}>
                    {days.length === 0 ? (
                      <Row gap={13} align={"center"}>
                        <p></p> {selectOn ? <ArrowUp /> : <ArrowDown />}
                      </Row>
                    ) : (
                      <p>{days.join(" ")}</p>
                    )}
                  </S.Btn2>
                  <Plus onClick={addTime} />
                </S.BtnRow>
                {dayarr &&
                  dayarr.map((item, i) => (
                    <S.Day key={i}>
                      <div>
                        {item[0].slice(0, 2) > 12 ? "" : ""}{" "}
                        {item[0].slice(0, 2) > 12
                          ? `${String(item[0].slice(0, 2) - 12).padStart(
                              2,
                              "0"
                            )}:${item[0].slice(3)}`
                          : item[0]}
                      </div>
                      <div>
                        {item[1].slice(0, 2) > 12 ? "" : ""}{" "}
                        {item[1].slice(0, 2) > 12
                          ? `${String(item[1].slice(0, 2) - 12).padStart(
                              2,
                              "0"
                            )}:${item[1].slice(3)}`
                          : item[1]}
                      </div>
                      <div>
                        {item[2].length == 1 ? item[2] : item[2].join(",")}
                      </div>
                      <p
                        onClick={() => {
                          deleteDay(item, i);
                        }}
                      >
                        
                      </p>
                    </S.Day>
                  ))}
              </S.TimeBox>
            </S.Box>
            <S.TextBox>
              <p></p>
              <p>       .</p>
              <p>
                        
                .
              </p>
            </S.TextBox>
          </S.Column>
          <S.Column>
            {selectOn && (
              <S.ComboBox x={"-400%"} y={"185%"}>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
              </S.ComboBox>
            )}
            <S.InputBox>
              <span>  </span>
              <input
                type="text"
                placeholder="Ex.   "
                name="etcTime"
                defaultValue={state?.businessHoursResDto?.etcTime}
                onChange={(e) => onChange(e)}
              />
            </S.InputBox>
            <S.InputBox>
              <span></span>
              <input
                type="text"
                name="phone"
                defaultValue={loc?.phone || state.phone}
                onChange={(e) => onChange(e)}
                placeholder="  "
              />
            </S.InputBox>
            <S.InputBox>
              <span></span>
              <input
                type="text"
                name="website"
                defaultValue={state.website}
                onChange={(e) => onChange(e)}
                placeholder="     "
              />
            </S.InputBox>
            <S.InputBox>
              <span> </span>
              <input
                type="text"
                placeholder="   "
                name="wifiPassword"
                defaultValue={state.wifiPassword}
                onChange={(e) => onChange(e)}
              />
            </S.InputBox>
          </S.Column>
        </Row>
      </S.Containaer>
      {searchModal && (
        <SearchModal
          setModal={setSearchModal}
          search={search}
          setSearch={setSearch}
          setLoc={setLoc}
          setRegister={setRegister}
          register={register}
        />
      )}
    </>
  );
};

export default Editcafe;
