import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import Paging from "../components/common/Pagination";
import { useNavigate } from "react-router-dom";

import * as S from "../pages/style";
import * as SS from "./noticesStyle";

import { eventImgApi, regImgApi } from "../util/events";

import { ReactComponent as Check } from "../svg/check.svg";

import BPreview from "./common/modal/Bpreview";
import PVImg from "./common/PVImg";
import Row from "../components/atoms/row";
import None from "./None";
import EventMapBox from "./eventMapBox";

const Event = () => {
  const navigate = useNavigate();

  const [page, setPage] = useState(1);
  const [sort, setSort] = useState("DESC");
  const [count, setCount] = useState(0);
  const [items, setItems] = useState(10);

  const [temp, setTemp] = useState([]);
  const [preview, setPreview] = useState(false);

  const [file, setFile] = useState();

  const handlePageChange = (page) => {
    setPage(page);
  };

  const onLoadFile = (e) => {
    if (file) {
      alert("  ");
    }
    if (e.target.files[0]) {
      setFile(e.target.files[0]);
    }
  };
  const regImg = () => {
    if (!file) {
      window.alert(" .");
    } else {
      regImgApi(file)
        .then((res) => {
          loadData();
          setFile();
        })
        .catch((err) => console.log(err));
    }
  };

  const sortData = (id) => {
    setSort(id);
  };

  const loadData = () => {
    eventImgApi(page, sort)
      .then((res) => {
        setTemp(res.data.data.eventResDtoList.dtoList);
        setCount(res.data.data.eventCnt);
      })
      .catch((err) => {
        console.log(err);
        window.alert("  ");
        navigate("/");
      });
  };

  useEffect(() => {
    loadData();
  }, [page, sort]);
  const input = useRef();
  return (
    <>
      <SS.Container>
        <div>
          <Row
            justify={"space-between"}
            align={"baseline"}
            style={{ marginBottom: "20px" }}
          >
            <Row gap={15}>
              <S.Sbtn id="DESC" onClick={(e) => sortData(e.target.id)}>
                
                {sort === "DESC" && <Check />}
              </S.Sbtn>
              <S.Sbtn id="ASC" onClick={(e) => sortData(e.target.id)}>
                 {sort === "ASC" && <Check />}
              </S.Sbtn>
            </Row>

            <Paging
              count={count}
              handlePageChange={handlePageChange}
              setPage={setPage}
              page={page}
            />
          </Row>
          {temp && (
            <>
              <EventMapBox
                temp={temp}
                items={items}
                page={page}
                i={0}
                loadData={loadData}
              />
              <EventMapBox
                temp={temp}
                items={items}
                page={page}
                i={2}
                loadData={loadData}
              />
              <EventMapBox
                temp={temp}
                items={items}
                page={page}
                i={4}
                loadData={loadData}
              />
              <EventMapBox
                temp={temp}
                items={items}
                page={page}
                i={6}
                loadData={loadData}
              />
              <EventMapBox
                temp={temp}
                items={items}
                page={page}
                i={8}
                loadData={loadData}
              />
            </>
          )}

          {temp.length === 0 && <None text={" "} />}
        </div>
        <SS.NewNotice>
          <p>  </p>
          <AttachBox>
            <p></p>
            {file ? (
              <ImgBox>
                <PVImg img={file} />
              </ImgBox>
            ) : (
              <>
                {" "}
                <InputBox
                  onClick={() => {
                    input.current?.click();
                  }}
                >
                  <p> </p>
                  <input
                    ref={input}
                    type="file"
                    accept="image/*"
                    style={{ display: "none" }}
                    onChange={(e) => onLoadFile(e)}
                  />
                </InputBox>
              </>
            )}
          </AttachBox>
          <Row gap={16} justify={"end"}>
            <SS.Btn back={"#515151"} onClick={() => setPreview(true)}>
              
            </SS.Btn>
            <SS.Btn back={"#2563eb"} onClick={regImg}>
              
            </SS.Btn>
          </Row>
        </SS.NewNotice>
      </SS.Container>
      {preview && <BPreview setModal={setPreview} item={file} />}
    </>
  );
};

const AttachBox = styled.div`
  background-color: #333333;
  margin-top: 30px;
  border-radius: 8px;
  width: 100%;
  height: 104px;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;

  & > p {
    font-weight: 500;
    color: #acacac;
    padding: 12px 16px;
  }
`;

const InputBox = styled.div`
  position: absolute;
  transform: translate(45%, 25%);
  border: 1px dotted #acacac;
  border-radius: 8px;
  height: 72px;
  margin: 0 auto;
  padding: 0 95px;
  line-height: 72px;
`;

const ImgBox = styled.div`
  position: absolute;
  transform: translate(45%, 25%);
  border-radius: 8px;
  width: 328px;
  max-height: 72px;
  margin: 0 auto;
  height: 72px;
`;

export default Event;
