import * as S from "./regSt";
import { useNavigate } from "react-router-dom";
import { useState, useRef } from "react";

import { useRecoilState } from "recoil";
import { registerState } from "../recoil/NcafeRegister";

import { ReactComponent as Cgood } from "../svg/Cgood.svg";
import { ReactComponent as Cbad } from "../svg/Cbad.svg";
import { ReactComponent as Csoso } from "../svg/Csoso.svg";
import { ReactComponent as Star } from "../svg/Star.svg";
import { ReactComponent as Photo } from "../svg/photo.svg";
import { ReactComponent as CloseIcon } from "../svg/close.svg";
import { ReactComponent as CCbad } from "../svg/CCbad.svg";
import { ReactComponent as CCgood } from "../svg/CCgood.svg";
import { ReactComponent as CCsoso } from "../svg/CCsoso.svg";
import { ReactComponent as Plus } from "../svg/plus.svg";
import { ReactComponent as ArrowDown } from "../svg/ArrowDown.svg";
import { ReactComponent as ArrowUp } from "../svg/ArrowUp.svg";
import { ReactComponent as Search } from "../svg/Search.svg";

import { feedCreateApi } from "../util/management";

import Row from "../components/atoms/row";
import Header from "../components/common/header";
import PVImg from "../components/common/PVImg";
import SearchModal from "../components/common/modal/SearchModal";

import { convertTime, updateDay } from "../hooks/registerHook";

const Register = () => {
  const navigate = useNavigate();
  const [file, setFile] = useState([]);
  const [register, setRegister] = useRecoilState(registerState);
  const [openTime, setOpenTime] = useState("");
  const [closeTime, setCloseTime] = useState("");
  const [selectOn, setSelectOn] = useState(false);
  const [search, setSearch] = useState("");
  const [loc, setLoc] = useState({ address_name: "" });
  const [searchModal, setSearchModal] = useState(false);
  const [days, setDays] = useState([]);
  const [dayarr, setDayarr] = useState([]);
  const socketText = [
    "  ",
    " ",
    " ",
    "  ",
  ];
  const wifiText = [
    "  ",
    "  ",
    "   ",
    "  ",
  ];
  const restroomText = [
    "  ",
    " ",
    "   ",
    " ",
  ];
  const tableSizeText = [
    "   ",
    "   ",
    "   ",
    "   ",
  ];

  const onChange = (e) => {
    const name = e.target.name;
    const copy = { ...register };
    copy[name] = e.target.value;
    setRegister(copy);
  };

  const onLoadFile = (e) => {
    let copy = [...file];
    if (copy.length >= 5) {
      window.alert(" 5  ");
      return;
    } else {
      if (e.target.files[0]) {
        copy = [...copy, e.target.files[0]];
        setFile(copy);
        const copy2 = { ...register };
        copy2.imageFiles = copy;
        setRegister(copy2);
      }
    }
  };

  const deleteImg = (idx) => {
    const copy = [...file];
    const copy2 = { ...register };
    copy.splice(idx, 1);
    copy2.imageFiles = copy;
    setFile(copy);
    setRegister(copy2);
  };

  const recommendChange = (e) => {
    const copy = { ...register };
    copy.recommendation = e.currentTarget.id;
    setRegister(copy);
  };

  const starChange = (e) => {
    const star = e.currentTarget.id;
    const name = e.currentTarget.parentNode.id;
    const copy = { ...register };
    copy[name] = Number(star);
    setRegister(copy);
  };

  const dayPush = async (e) => {
    e.stopPropagation();
    const copy = [...days];
    if (!days.includes(e.currentTarget.id)) {
      copy.push(e.currentTarget.id);
      setDays(copy);
    } else {
      copy.splice(days.indexOf(e.currentTarget.id), 1);
      setDays(copy);
    }
  };

  const deleteDay = async (a, i) => {
    const copy2 = { ...register };
    for await (let item of a[2]) {
      updateDay(item, copy2, "", "");
    }
    setRegister(copy2);
    const copy = [...dayarr];
    copy.splice(i, 1);
    setDayarr(copy);
  };

  const addTime = async () => {
    const copy = [...dayarr];
    const copy2 = { ...register };
    const open = await convertTime(openTime);
    const close = await convertTime(closeTime);
    for await (let item of days) {
      updateDay(item, copy2, open, close);
    }
    setRegister(copy2);
    copy.push([open, close, days]);
    setDayarr(copy);
    setOpenTime("");
    setCloseTime("");
    setDays([]);
    setSelectOn(false);
  };

  const submit = async (register) => {
    console.log(register);
    if (!register.storeName || !register.siNm) {
      window.alert(",  !");
    } else if (
      !register.recommendation ||
      !register.wifi ||
      !register.restroom ||
      !register.tableSize ||
      !register.socket
    ) {
      window.alert(
        "  ?    "
      );
    }

    feedCreateApi(register)
      .then((res) => navigate("/management"))
      .catch((err) => console.log(err));
  };
  const input = useRef();

  return (
    <>
      <Header
        align={"center"}
        mcolor={"#8B8B8B"}
        text={" "}
        inner={"  "}
      >
        <S.Submit
          disalbed={
            register.storeName == null ||
            register.siNm == null ||
            register.recommendation == null ||
            register.wifi == null ||
            register.restroom == null ||
            register.tableSize == null ||
            register.socket == null
          }
          isFill={
            register.storeName &&
            register.siNm &&
            register.recommendation &&
            register.wifi &&
            register.restroom &&
            register.tableSize &&
            register.socket
          }
          onClick={() => submit(register)}
        >
          
        </S.Submit>
      </Header>
      <S.Containaer>
        <Row gap={20}>
          <S.Column>
            <S.InputBox>
              <span></span>
              <Search onClick={() => setSearchModal(!searchModal)} />
              <input
                name="storeName"
                type="text"
                value={search}
                onChange={(e) => {
                  onChange(e);
                  setSearch(e.target.value);
                }}
              />
            </S.InputBox>
            <S.InputBox>
              <span></span>
              <div>{loc.address_name} </div>
            </S.InputBox>

            <S.Box height={176}>
              <p>  ?</p>
              <S.RowBox>
                <div
                  id="BAD"
                  onClick={(e) => {
                    recommendChange(e);
                  }}
                >
                  {register.recommendation === "BAD" ? <CCbad /> : <Cbad />}
                  <p></p>
                </div>
                <div
                  id="NORAML"
                  onClick={(e) => {
                    recommendChange(e);
                  }}
                >
                  {register.recommendation === "NORAML" ? (
                    <CCsoso />
                  ) : (
                    <Csoso />
                  )}
                  <p> </p>
                </div>
                <div
                  id="GOOD"
                  onClick={(e) => {
                    recommendChange(e);
                  }}
                >
                  {register.recommendation === "GOOD" ? <CCgood /> : <Cgood />}
                  <p></p>
                </div>
              </S.RowBox>
            </S.Box>
            <S.Box height={304}>
              <p>   </p>
              <S.ColumnBox>
                <div>
                  <p></p>
                  <S.StarBox id="socket" isFill={register.socket}>
                    <Star id={1} onClick={(e) => starChange(e)} />
                    <Star id={2} onClick={(e) => starChange(e)} />
                    <Star id={3} onClick={(e) => starChange(e)} />
                    <Star id={4} onClick={(e) => starChange(e)} />
                  </S.StarBox>
                  {register.socket && <p>{socketText[register.socket - 1]}</p>}
                </div>
                <div>
                  <p></p>
                  <S.StarBox id="wifi" isFill={register.wifi}>
                    <Star id={1} onClick={(e) => starChange(e)} />
                    <Star id={2} onClick={(e) => starChange(e)} />
                    <Star id={3} onClick={(e) => starChange(e)} />
                    <Star id={4} onClick={(e) => starChange(e)} />
                  </S.StarBox>
                  {register.wifi && <p>{wifiText[register.wifi - 1]}</p>}
                </div>
                <div>
                  <p></p>
                  <S.StarBox id="restroom" isFill={register.restroom}>
                    <Star id={1} onClick={(e) => starChange(e)} />
                    <Star id={2} onClick={(e) => starChange(e)} />
                    <Star id={3} onClick={(e) => starChange(e)} />
                    <Star id={4} onClick={(e) => starChange(e)} />
                  </S.StarBox>
                  {register.restroom && (
                    <p>{restroomText[register.restroom - 1]}</p>
                  )}
                </div>
                <div>
                  <p></p>
                  <S.StarBox id="tableSize" isFill={register.tableSize}>
                    <Star id={1} onClick={(e) => starChange(e)} />
                    <Star id={2} onClick={(e) => starChange(e)} />
                    <Star id={3} onClick={(e) => starChange(e)} />
                    <Star id={4} onClick={(e) => starChange(e)} />
                  </S.StarBox>
                  {register.tableSize && (
                    <p>{tableSizeText[register.tableSize - 1]}</p>
                  )}
                </div>
              </S.ColumnBox>
            </S.Box>
            <S.TextBox>
              <p></p>
              <p>       .</p>
              <p>
                        
                .
              </p>
            </S.TextBox>
          </S.Column>
          <S.Column>
            <S.Box height={168}>
              <p> </p>
              <S.PhotoBox>
                <S.FileUpload
                  onClick={() => {
                    input.current?.click();
                  }}
                >
                  <Photo />
                  <div>{file.length}/5</div>
                  <input
                    ref={input}
                    type="file"
                    accept="image/*"
                    style={{ display: "none" }}
                    onChange={(e) => onLoadFile(e)}
                  />
                </S.FileUpload>
                {file?.map((a, i) => {
                  return (
                    <S.ImgBox key={i}>
                      <CloseIcon name={a.name} onClick={() => deleteImg(i)} />
                      <PVImg img={a} />
                    </S.ImgBox>
                  );
                })}
              </S.PhotoBox>
            </S.Box>
            <S.Box height={128}>
              <p></p>
              <S.TimeBox>
                <S.BtnRow gap={13}>
                  <S.Btn>
                    {openTime && (
                      <p>{openTime.slice(0, 2) > 12 ? "" : ""}</p>
                    )}
                    <input
                      placeholder=" "
                      value={openTime}
                      onChange={(e) => {
                        setOpenTime(e.target.value);
                      }}
                    />
                  </S.Btn>
                  <S.Btn>
                    {closeTime && (
                      <p>{closeTime.slice(0, 2) > 12 ? "" : ""}</p>
                    )}
                    <input
                      placeholder=" "
                      value={closeTime}
                      onChange={(e) => {
                        setCloseTime(e.target.value);
                      }}
                    />
                  </S.Btn>
                  <S.Btn2 isT={selectOn} onClick={() => setSelectOn(!selectOn)}>
                    {days.length === 0 ? (
                      <Row gap={13} align={"center"}>
                        <p></p> {selectOn ? <ArrowUp /> : <ArrowDown />}
                      </Row>
                    ) : (
                      <p>{days.join(" ")}</p>
                    )}
                  </S.Btn2>
                  <Plus onClick={addTime} />
                </S.BtnRow>
                {dayarr.map((item, i) => (
                  <S.Day key={i}>
                    <div>
                      {item[0].slice(0, 2) > 12 ? "" : ""}{" "}
                      {item[0].slice(0, 2) > 12
                        ? `${String(item[0].slice(0, 2) - 12).padStart(
                            2,
                            "0"
                          )}:${item[0].slice(3)}`
                        : item[0]}
                    </div>
                    <div>
                      {item[1].slice(0, 2) > 12 ? "" : ""}{" "}
                      {item[1].slice(0, 2) > 12
                        ? `${String(item[1].slice(0, 2) - 12).padStart(
                            2,
                            "0"
                          )}:${item[1].slice(3)}`
                        : item[1]}
                    </div>
                    <div>
                      {item[2].length == 1 ? item[2] : item[2].join(",")}
                    </div>
                    <p
                      onClick={() => {
                        deleteDay(item, i);
                      }}
                    >
                      
                    </p>
                  </S.Day>
                ))}
              </S.TimeBox>
            </S.Box>
            {selectOn && (
              <S.ComboBox>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
                <div id="" onClick={(e) => dayPush(e)}>
                  
                </div>
              </S.ComboBox>
            )}
            <S.InputBox>
              <span>  </span>
              <input
                type="text"
                placeholder="Ex.   "
                name="etcTime"
                defaultValue={register.etcTime}
                onChange={(e) => onChange(e)}
              />
            </S.InputBox>
            <S.InputBox>
              <span></span>
              <input
                type="text"
                name="phone"
                defaultValue={loc?.phone}
                onChange={(e) => onChange(e)}
                placeholder="  "
              />
            </S.InputBox>
            <S.InputBox>
              <span></span>
              <input
                type="text"
                name="website"
                defaultValue={register.website}
                onChange={(e) => onChange(e)}
                placeholder="     "
              />
            </S.InputBox>
            <S.InputBox>
              <span> </span>
              <input
                type="text"
                placeholder="   "
                name="wifiPassword"
                defaultValue={register.wifiPassword}
                onChange={(e) => onChange(e)}
              />
            </S.InputBox>
          </S.Column>
        </Row>
      </S.Containaer>
      {searchModal && (
        <SearchModal
          setModal={setSearchModal}
          search={search}
          setSearch={setSearch}
          setLoc={setLoc}
          setRegister={setRegister}
          register={register}
        />
      )}
    </>
  );
};

export default Register;
