import Header from "../components/common/header";
import * as S from "./style copy";
import styled from "styled-components";

import Row from "../components/atoms/row";

import Paging from "../components/common/Pagination";

import { ReactComponent as Search } from "../svg/Search.svg";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { ReactComponent as Memo } from "../svg/memo.svg";
import { ReactComponent as Check } from "../svg/check.svg";
import { ReactComponent as ArrowDown } from "../svg/ArrowDown.svg";

import MReview from "../components/common/modal/MReview";
import {
  reviewDataApi,
  reviewDetailApi,
  reviewSearchApi,
} from "../util/review";
import None from "../components/None";
import DropBox from "../components/common/dropbox";
import ReviewTemp from "../components/reviewTemp";
import MemoModal from "../components/common/modal/memo";

const Review = () => {
  const [sort, setSort] = useState("DESC");
  const navigate = useNavigate();
  const [temp, setTemp] = useState([]);

  const [search, setSearch] = useState("");
  // pagination
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);
  const [items, setItems] = useState(9);

  //drop
  const [isDrop, setIsDrop] = useState(false);
  const [selected, setSelected] = useState("");
  const [arr, setArr] = useState(["", " ", " "]);

  const [modal, setModal] = useState(false);

  const [memoId, setMemoId] = useState(null);
  const [memoModal, setMemoModal] = useState(false);
  const [selectItem2, setSelectItem2] = useState([]);

  const handlePageChange = (page) => {
    setPage(page);
  };

  const onModal = (item) => {
    reviewDetailApi(item.reviewId)
      .then((res) => {
        setSelectItem2(res.data.data);
        setModal(!modal);
      })
      .catch((err) => console.log(err));
  };

  const sortData = (id) => {
    setSort(id);
  };
  const onclick = () => {
    setSort("DESC");
    setPage(1);
    searchData();
  };

  const searchData = () => {
    if (selected === "") {
      reviewSearchApi(search, "w", page, sort).then((res) => {
        setTemp(res.data.data.reviewResDtoList.dtoList);
      });
    }
    if (selected === " ") {
      reviewSearchApi(search, "c", page, sort).then((res) => {
        console.log(res);
        setTemp(res.data.data.reviewResDtoList.dtoList);
      });
    }
    if (selected === " ") {
      reviewSearchApi(search, "s", page, sort).then((res) =>
        setTemp(res.data.data.reviewResDtoList.dtoList)
      );
    }
  };

  const changeData = () => {
    if (selected === "") {
      reviewDataApi(page, sort)
        .then((res) => {
          setCount(res.data.data.reviewCnt);
          setTemp(res.data.data.reviewResDtoList.dtoList);
        })
        .catch((err) => console.log(err));
    } else {
      searchData();
    }
  };

  useEffect(() => {
    changeData();
  }, [page, sort]);
  return (
    <>
      <Header
        mcolor={"#fff"}
        text={" "}
        subText={`  ${count}`}
      />
      <Row
        justify={"space-between"}
        align={"baseline"}
        style={{ marginBottom: "20px" }}
      >
        <Row gap={15}>
          <S.Sbtn id="DESC" onClick={(e) => sortData(e.target.id)}>
            
            {sort === "DESC" && <Check />}
          </S.Sbtn>
          <S.Sbtn id="ASC" onClick={(e) => sortData(e.target.id)}>
             {sort === "ASC" && <Check />}
          </S.Sbtn>
        </Row>
        <Row gap={15} align={"baseline"}>
          <Paging
            count={count}
            handlePageChange={handlePageChange}
            setPage={setPage}
            page={page}
          />
          <S.Sbtn onClick={() => setIsDrop(!isDrop)}>
            <p>{selected}</p>
            <ArrowDown />
          </S.Sbtn>

          {isDrop && (
            <DropBox
              arr={arr}
              setIsDrop={setIsDrop}
              setArr={setArr}
              selected={selected}
              setSelected={setSelected}
            />
          )}
          <Row style={{ borderBottom: "1px solid #fff" }}>
            <S.Input
              placeholder=""
              type="text"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            <Search onClick={onclick} />
          </Row>
        </Row>
      </Row>
      <S.Wrapper isNull={temp.length === 0}>
        <TableHeader>
          <div></div>
          <div></div>
          <div> </div>
          <div> </div>
          <div> </div>
          <div></div>
          <div> </div>
          <div></div>
        </TableHeader>

        <ReviewTemp
          temp={temp}
          onModal={onModal}
          setMemoModal={setMemoModal}
          setMemoId={setMemoId}
          setSelectItem={setSelectItem2}
        />
      </S.Wrapper>

      {memoModal && (
        <MemoModal
          memoId={memoId}
          setModal={setMemoModal}
          selectItem={selectItem2}
        />
      )}
      {temp.length === 0 && <None text={""} />}
      {modal && <MReview setModal={setModal} selectItem2={selectItem2} />}
    </>
  );
};

const TableHeader = styled.div`
  font-size: 14px;
  display: flex;
  width: 100%;
  color: #8b8b8b;
  text-align: center;
  line-height: 42px;
  border-bottom: 1px solid #515151;
  & > div {
    flex: 0.7;
    border-right: 1px solid #515151;
  }
  & > div:nth-child(3) {
    flex: 4;
  }
  & > div:nth-child(5) {
    flex: 1.5;
  }

  & > div:last-child {
    flex: 0.5;
    border-right: none;
  }
`;

export default Review;
